html,
body {
    margin: 0;
    padding: 0;
    font-family: "Noto Sans JP", "游ゴシック Medium", "Yu Gothic Medium", "Original Yu Gothic", "Yu Gothic", YuGothic, Verdana, Meiryo, sans-serif;
    height: 100%;
    color: $main-color-font;
}

html {
    overflow-x: hidden;
}

body{
  @include screen($screenMedium){
    background: #ffffff;
  }
}

* {
    box-sizing: border-box;
}

body {
    background: #eff3f6;
}

a {
    color: inherit;
}

img {
    max-width: 100%;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    @include transition(all $duration ease);
    color: $main-color;

    &:hover {
        opacity: 0.8;
    }
}

.fleft {
    float: left;
}

.fright {
    float: right;
}

.only_sp {
    display: none;

    @include screen($screenSmall) {
        display: block;
    }
}

.only_pc {
    @include screen($screenSmall) {
        display: none;
    }
}

.align-left{
  text-align: left;
}

.align-right{
  text-align: right;
}

.align-center{
  text-align: center;
}

#wrapper {
    height: 100%;
}

.container{
  max-width: 1200px;
  margin: 0 auto;
}
