.d-btn__toggleSidebar {
    position: absolute;
    left: 100%;
    @include dis(top);
    width: 50px;
    height: 50px;
    line-height: 50px;
    top: -1px;
    margin-left: 1px;
    text-align: center;
    z-index: 99;

    i.fas {
        font-size: 30px;
        color: $btn-toggle;
        display: inline-block;
        vertical-align: middle;
        margin-top: -5px;
    }

    @include screen($screenLarge) {
        position: fixed;
        right: 0;
        left: auto;

        i.fas {
            color: $btn-toggle;
        }
    }
}

.btn.btn-offer {
    display: block;
    background-color: $main-color;
    padding: 20px 0;
    margin-bottom: 50px;
    border-radius: 10px;
    font-size: 20px;
    color: #ffffff;
    text-decoration: none;
    transition: all 800ms ease;

    &:hover {
        opacity: 0.8;
    }

    a {
        text-decoration: none;
        display: block;
        color: #fff;

        @include screen($screenSmall) {
            padding: 0px 15px;
            font-size: 14px;
        }
    }
}
