#page-feature {
    .page__head {
        position: relative;
        padding-bottom: 0;
        margin-bottom: 30px;

        h3 {
            text-align-last: left;
            background: #4699D6;
            color: #fff;
            font-weight: 400;
            padding: 10px 15px;
            position: static;
            @include dis(top);

            &:after {
                border-style: solid;
                border-width: 1px;
                border-color: #4699D6;
                width: 100%;
                height: 0;
                left: 0;
                transform: translateX(0%);
            }
        }
        @include screen($screenLarge) {
          padding-left: 0px;
          padding-right: 0px;
        }
    }

    .page__content {
        padding-left: 0px;
        padding-right: 0px;

        li {
            .feature__intro h6 {
                text-align: left;
            }
        }
    }
}

.list-item {
    li {
        display: flex;
        margin-bottom: 30px;
        padding: 30px;
        border-bottom: 1px solid #ccc;
        border-radius: 3px;
        box-shadow: 0 0 5px #ccc;

        .tooltip {
            margin-right: 40px;
        }

        .feature__thumbnail {
            display: inline-block;
            width: 300px;
            height: 300px;
            min-width: 300px;
            position: relative;
            overflow: hidden;
            border-radius: 50%;
            margin: 0;
            border: 5px solid #fff;
            box-shadow: 0 0 5px #9c9999;
        }

        .feature__intro {
            margin: auto 0;

            h6 {
                font-size: 22px;
                margin: 0 0 30px;
            }

            p {
                text-align: justify;
                vertical-align: middle;
                margin: 0 auto;
            }
        }
        @include screen($screenSmall){
          display: block;
          margin-bottom: 35px;
          padding-bottom: 35px;
          .tooltip{
            margin-right: 0;
            margin-left: 0;
          }
        }
    }
}
