.list-adovisors {
  font-size: 0;
  display: flex;
  flex-wrap: wrap;
  li {
    display: inline-block;
    vertical-align: top;
    width: 19%;
    margin-right: 0.5%;
    margin-left: 0.5%;
    padding: 20px 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 5px #ccc;
    @include screen($screenLarge) {
      flex: 1 0 33%;
      margin-bottom: 15px;
    }
    @include screen($screenSmall) {
      flex: 1 0 100%;
    }
  }
  &:after {
    content: "";
    display: block;
    clear: both;
  }
  .adovisors__thumbnail {
    width: 126px;
    height: 126px;
    line-height: 126px;
    border-radius: 50%;
    border: solid 3px #e7e7e7;
    margin: 0px auto 15px;
    text-align: center;
    overflow: hidden;
    background: #fff;
    font-size: 0;
    img {
      width: 100%;
      display: block;
    }
  }
  .adovisors__intro {
    h5 {
      text-transform: uppercase;
      color: $main-color;
      font-size: 18px;
      margin: 0 0 15px;
      position: relative;
      &:after {
        content: "";
        display: block;
        width: 30%;
        margin: 0px auto;
        height: 2px;
        font-weight: 400;
        background: $main-color;
        margin-top: 10px;
      }
    }
    p {
      font-size: 16px;
    }
  }
}
