.list-graduated{
  &:after{
    content: '';
    display: block;
    clear: both;
  }
  li{
    border: 1px solid #ccc;
    box-shadow: 0 0 5px #ccc;
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 15px;
    &:after{
      content: '';
      display: block;
      clear: both;
    }
  }
  .graduated__intro{
    font-size: 0;
    float: left;
    margin-right: 1%;
    width: 25%;
    .graduated__intro__name{
      display: inline-block;
      vertical-align: top;
      font-size: 25px;
      font-weight: 700;
      line-height: 60px;
    }
    @include screen($screenSmall){
      width: 100%;
    }
  }
  .graduated__intro__thumbnail{
    overflow: hidden;
    max-height: 350px;
    margin-top: 10px;
    img{
      display: block;
    }
  }
  .graduated__content{
    font-size: 14px;
    float: left;
    width: 73%;
    margin-left: 1%;
    text-align: justify;
    p{
      font-size: 17px;
    }
    @include screen($screenSmall){
      width: 100%;
      margin-top: 15px;
    }
    .graduated__intro__school{
      text-align: center;
      font-size: 20px;
      margin-bottom: 20px;
      padding: 0px 20px;
      b{
        font-weight: 400;
        display: block;
        margin-bottom: 10px;
      }
      strong{
        font-weight: 400;
        display: block;
      }
    }
  }
}
