.accordions-list {
  text-align: left;

  .accordion.toggle-accordion {
    margin-bottom: 40px;
  }
}

.toggle-accordion {
  .accordion__title {
    display: block;
    background-color: #4b4c4d;
    position: relative;
    font-size: 14px;
    margin: 0;
    a {
      pointer-events: none;
    }

    &:before {
      content: "\f067";
      font-family: "FontAwesome";
      float: right;
      font-size: 10px;
      position: absolute;
      top: 50%;
      right: 10px;
      line-height: 0;
      color: #fff;
      font-size: 15px;
    }

    &.active {
      background: #3498db;
      &:before {
        content: "\f068";
      }
    }

    a {
      text-decoration: none;
      color: #fff;
      padding: 10px 15px;
      line-height: 22px;
      display: block;
    }
  }

  .accordion__inner {
    padding: 15px;
    display: none;
    p {
      margin: 0;
    }
  }
}
