.timeline {
  position: relative;
  padding-top: 3rem;
  padding-bottom: 3rem;
  &:before {
    content: "";
    width: 4px;
    height: 100%;
    background-color: $main-color;
    position: absolute;
    top: 30px;
  }
}

.timeline__group {
  position: relative;
  &:not(:first-of-type) {
    margin-top: 4rem;
  }
}

.timeline__year {
  padding: 10px 20px;
  color: #fff;
  font-size: 35px;
  background-color: #3498db;
  position: absolute;
  left: 0;
  top: 0;
  font-weight: 700;
  margin-top: -15px;
  text-transform: uppercase;
}

.timeline__box {
  position: relative;
  &:not(:last-of-type) {
    margin-bottom: 30px;
  }
  &:before {
    content: "";
    width: 100%;
    height: 2px;
    background-color: $main-color;
    position: absolute;
    left: 0;
    z-index: -1;
  }
}

.timeline__date {
  width: 170px;
  position: absolute;
  left: 0;
  box-sizing: border-box;
  padding: .5rem 1.5rem;
  text-align: center;
  background-color: $main-color;
  color: #fff;
}

.timeline__day {
  font-size: 17px;
  font-weight: 700;
  display: block;
}

.timeline__month {
  display: block;
  font-size: .8em;
  text-transform: uppercase;
}

.timeline__post {
  padding: 1.5rem 2rem;
  border-radius: 2px;
  border-left: 3px solid $main-color;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);
  background-color: #fff;
  &.timeline__post--special{
    border: none;
    box-shadow: none;
    text-align: center;
    background: none;
    h5{
      font-size: 50px;
      @include screen($screenSmall){
        font-size: 30px;
      }
    }
  }
}

@media screen and (min-width: 641px) {
  .timeline:before {
    left: 30px;
  }
  .timeline__group {
    padding-top: 55px;
  }
  .timeline__box {
    padding-left: 150px;
    &:before {
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .timeline__date {
    top: 50%;
    margin-top: -27px;
  }
}

@media screen and (max-width: 640px) {
  .timeline:before {
    left: 0;
  }
  .timeline__group {
    padding-top: 40px;
  }
  .timeline__box {
    padding-left: 20px;
    padding-top: 70px;
    &:before {
      top: 90px;
    }
  }
  .timeline__date {
    top: 0;
  }
}

.timeline {
  --timelineMainColor: #4557bb;
  font-size: 16px;
}

@media screen and (min-width: 768px) {
  html {
    font-size: 62.5%;
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: 55%;
  }
}

p {
  margin-top: 0;
  margin-bottom: 1.5rem;
  line-height: 1.5;
  &:last-child {
    margin-bottom: 0;
  }
}

.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.page__demo {
  flex-grow: 1;
}

.main-container {
  margin-left: auto;
  margin-right: auto;
}

.page__container {
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer {
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: center;
  font-size: 1.4rem;
}

.footer__link {
  text-decoration: none;
  color: inherit;
}

@media screen and (min-width: 361px) {
  .footer__container {
    display: flex;
    justify-content: space-between;
  }
}

@media screen and (max-width: 360px) {
  .melnik909 {
    display: none;
  }
}
