.navbar {
    background: #333;
    font-family: 'Fjalla One', sans-serif;

    &.navbar-fixed-left {
        width: $navbar-width * 1px;
        position: fixed;
        border-radius: 0;
        z-index: 999;
        height: 100%;
        background: #222c3c;
        left: -($navbar-width * 1px);
        top: 0;
        @include transition(left $duration ease);

        &+.content-wrap {
            // left: 0;
            padding-left: 0;
            width: 100%;
            position: relative;
            @include transition(padding-left $duration ease);
            height: 100%;
        }

        &.slide-navbar {
            left: 0px;

            &+main.content-wrap {
                // left: $navbar-width * 1px;
                padding-left: $navbar-width * 1px;
            }
        }

        .navbar-inner {
            height: 100%;
            width: 100%;
            overflow-x: hidden;
            overflow-y: auto;
        }

        @include screen($screenLarge) {
            width: auto;
            left: 0;
            right: 0;
            top: 0;
            height: auto;

            .navbar-logo {
                padding: 0;
                margin: 0;

                h2 {
                    margin: 0;
                    font-size: 20px;
                    line-height: 50px;
                }
            }

            &+.content-wrap {
                padding-top: 50px;
            }

            &.slide-navbar {
                left: 0px;

                &+main.content-wrap {
                    // left: 0;
                    padding-left: 0px;

                }

                .navigation-bar {
                    max-height: 1000px;
                }
            }
        }
    }

    .navbar-logo {
        text-align: center;
        font-size: 14px;
        color: #fff;
        line-height: 40px;
        font-weight: 300;
        margin: 18px 0 27px;
        text-decoration: none;

        h2 {
            font-weight: 400;
            font-size: 25px;
        }
    }
}

.navigation-bar {
    padding: 0 15px;

    .navbar-nav {
        margin: 0;
        padding: 0;
        list-style: none;
        float: none;
        display: block;
        text-transform: capitalize;

        li {
            display: block;
            float: none;
            color: #97a0ad;
            font-weight: 300;
            margin: 0 0 20px;

            .navbar-label {
                font-size: 14px;
                color: #e8e9eb;
                padding: 0px 17px;
                display: block;
                margin-bottom: 10px;
                text-transform: uppercase;
            }

            a {
                padding: 0;
                @include dis(top);
                line-height: 28px;
                padding: 0 16px;
                border-radius: 5px;
                text-decoration: none;
                @include transition(all $duration ease);
                color: #97a0ad;
                cursor: pointer;
                font-size: 14px;
                text-transform: uppercase;
                display: block;
            }

            &.active>a,
            &:hover>a {
                background: $main_color;
                color: #fff;
            }

            li {
                margin-bottom: 4px;
            }
        }
    }

    @include screen($screenLarge) {
        @include transition(all $duration ease);
        max-height: 0px;
        overflow: hidden;
    }
}
