.tooltip {
    position: relative;
    display: inline-block;

    .tooltiptext {
        visibility: hidden;
        width: 120px;
        background-color: rgba(0, 0, 0, 0.6);
        color: #fff;
        text-align: center;
        padding: 5px 0;
        -webkit-border-radius: 6px;
        border-radius: 6px;
        position: absolute;
        z-index: 1;
        bottom: 100%;
        left: 50%;
        margin-left: -60px;
        margin-bottom: 7px;
        &:after{
          content: '';
          left: 50%;
          top: 100%;
          margin-left: -7px;
          border-style: solid;
          border-width: 7px;
          border-color: rgba(0, 0, 0, 0.6) transparent transparent transparent;
          position: absolute;

        }
    }

    &:hover .tooltiptext {
        visibility: visible;
    }
}
