#page-inquiry{
  .page__content{
    h2{
      font-size: 35px;
      margin-bottom: 30px;
      border-bottom: 3px solid #ccc;
      padding: 0px 0px 20px;
      color: $main-color;
    }
    i.far{
      color: $main-color;
      display: inline-block;
      vertical-align: middle;
      margin-right: 10px;
      font-size: 20px;
    }
    p{
      color: #888;
      margin-top: 10px;
      margin-bottom: 30px;
      a{
        display: block;
        padding-left: 30px;
        padding-top: 10px;
        padding-bottom: 10px;
      }
    }
  }
}
