.template-page {
    .page__head {
        position: relative;
        padding-bottom: 0;
        margin-bottom: 30px;
        padding-top: 70px;
        margin-left: 15px;
        margin-right: 15px;
        // h3 {
        //     text-transform: uppercase;
        //     text-align: center;
        //     font-size: 30px;
        //     position: relative;
        //     padding: 10px 0px;
        //     margin: 0;

        //     &:after {
        //         content: '';
        //         width: 200px;
        //         height: 3px;
        //         border-top: 2px solid #333;
        //         border-bottom: 2px solid #333;
        //         left: 50%;
        //         position: absolute;
        //         top: 100%;
        //         transform: translateX(-50%);
        //     }
        // }
        h3 {
            text-align-last: left;
            background: #4699D6;
            text-transform: uppercase;
            color: #fff;
            font-size: 20px;
            font-weight: 400;
            padding: 10px 15px;
            position: static;
            @include dis(top);
            margin: 0;

            &:after {
                content: '';
                border-style: solid;
                border-width: 1px;
                border-color: #4699D6;
                width: 100%;
                height: 0;
                left: 0;
                transform: translateX(0%);
                position: absolute;
                top: 100%;
            }
        }

        @include screen($screenSmall) {
            padding: 20px 0px 0px;
        }
    }

    .page__content {
        text-align: center;
        padding: 40px 0px 50px;
        line-height: 30px;
        font-size: 16px;
        margin-left: 15px;
        margin-right: 15px;

        h5 {
            font-size: 27px;
            margin: 0 0 30px;

            @include screen($screenMedium) {
                font-size: 23px;
            }
        }

        p {
            margin: 0;

            &.first-letter {
                text-align: left;
                margin-bottom: 20px;
                &.align-center{
                  text-align: center;
                  margin: 40px 0 -20px;
                }
                &::first-letter {
                    // initial-letter: 2;
                    // font-size: 50px;
                    // line-height: 50px;
                    // margin-right: 10px;
                    // float: left;
                }
            }
        }

        ul {
            margin: 10px 0 0;
            padding: 0;
            list-style: none;

            &.list-note {
                margin: 30px 0px;
            }

            li {
                a {
                    color: $main-color;
                    text-decoration: underline;
                }
            }
        }

        @include screen($screenMedium) {
            // padding-left: 15px;
            // padding-right: 15px;
        }

        @include screen($screenSmall) {
            // padding-left: 15px;
            // padding-right: 15px;
        }

    }
}

.list-people {
    &:after {
        content: '';
        clear: both;
        display: block;
    }

    li {
        float: left;
        width: 49%;
        text-align: center;
        display: flex;
        box-shadow: 0 0 5px #ccc;
        border: 1px solid #ccc;
        border-radius: 5px;
        padding: 15px 15px;
        margin-right: 1%;
        margin-bottom: 20px;
        &:nth-child(even){
          margin-right: 0;
          margin-left: 1%;
        }

        @include screen($screenMedium) {
            width: 49%;
        }

        @include screen($screenSmall) {
            width: 100%;
            margin-left: 0;
            margin-right: 0;
            &:nth-child(even){
              margin-right: 0;
              margin-left: 0;
            }
        }

        @include screen(480px) {
            display: block;
            text-align: center;
        }
    }

    .people__thumbnail {
        min-width: 168px;
        width: 168px;
        height: 168px;
        border-radius: 50%;
        overflow: hidden;
        margin: 0 15px 0 0;
        border: 5px solid #fff;
        box-shadow: 0 0 5px #9c9999;

        img {
            // height: 100%;
        }

        @include screen(480px) {
            margin: 0px auto;
        }
    }

    .people__intro {
        text-align: left;
        margin: auto 0px;

        h5 {
            font-size: 20px;
            margin: 0 0 15px;
            border-bottom: 2px solid #3498db;
            color: #3498db;

            span.small-text {
                font-size: 11px;
                @include dis(bottom);
                line-height: normal;
            }
            @include screen($screenSmall) {
              margin-top: 15px;
              border-bottom: none;
              position: relative;
              padding-bottom: 10px;
              &:after{
                content: '';
                width: 100px;
                top: 100%;
                position: absolute;
                left: 50%;
                top: 100%;
                margin-left: -50px;
                height: 2px;
                background: #3498db;
              }
            }
        }

        p {
            margin: 0;
        }

        @include screen(480px) {
            text-align: center;
        }
    }
}

.list-note {
    text-align: left;

    li {
        font-size: 18px;
        margin-bottom: 10px;
    }
}

.page__content__thread{
  h2{
    margin-bottom: 40px;
  }
}
