.page__theme {
  .page__head h3 {
    &:after {
      width: 100%;
      // max-width: 380px;
    }
  }

  .page__content {
    .color-red {
      color: #ff0000;
    }

    p {
      &[class^="indent"] {
        font-size: 18px;

        @include screen($screenSmall) {
          text-indent: 0;
          text-align: center;
        }
      }

      br {
        display: none;

        @include screen($screenSmall) {
          display: block;
        }
      }
      &.first-letter{
        text-align: center;
        br{
          display: block;
        }
      }
    }
  }
}
