#page-about {
    .page__head {
        position: relative;
        padding-bottom: 0;
        margin-bottom: 30px;
        padding-top: 70px;

        h3 {
            text-align-last: left;
            background: #4699D6;
            color: #fff;
            font-weight: 400;
            padding: 10px 15px;
            position: static;
            @include dis(top);

            &:after {
                border-style: solid;
                border-width: 1px;
                border-color: #4699D6;
                width: 100%;
                height: 0;
                left: 0;
                transform: translateX(0%);
            }
        }

        @include screen($screenLarge) {
            padding-left: 0px;
            padding-right: 0px;
        }
    }

    .page__content {
        padding-left: 0;
        padding-right: 0;

        h5 {
            font-size: 30px;
        }

        @include screen($screenLarge) {
          margin-right: 5px;
          margin-left: 5px;
          padding-left: 15px;
          padding-right: 15px;
        }
    }
}
