#page-home{
  background: none;
  img{
    &.img-pc{
      display: block;
      @include screen($screenLarge){
        display: none;
      }
    }
    &.img-tb{
      display: none;
      @include screen($screenLarge){
        display: block;
      }
    }
  }
  .container{
    padding-top: 40px;
    padding-left: 25px;
    padding-right: 25px;
    .btn.btn-offer{
      text-align: center;
    }
  }
}
